<template>
	<div class="section">
		<div class="section-one">
			<section-one :dataOne="indexData"></section-one>
			<div class="secOneData">
				<p class='p_title'>{{indexData.pTitle}}</p>
				<p class='p_sub'>{{indexData.pSubDate}}&nbsp;&nbsp;&nbsp;&nbsp;{{indexData.pSubTitle}}</p>
				<div class="bottom_data">
					<el-row>
						<el-col :span="4" v-for="(colData,index) of indexData.colDatas" :key="index">
							<div class="grid-content">
								<p class="col_val">{{colData.colVal}}</p>
								<p class="col_name">{{colData.colName}}</p>
							</div>
						</el-col>
					</el-row>
				</div>
			</div>
		</div>

		<div class="section-two">
			<el-card :body-style="{ padding: '0px' }" ref="twoLeft">
				<div class="swiper-container" id="swiper">
					<div class="swiper-wrapper">
						<div class="swiper-slide" v-for="(item) of indexData.lunboImg">
							<figure>
								<img style="width: 100%;" v-cloak :src="item.url" />
								<figcaption class="img_info">
									<h2>{{item.info}}</h2>
									<p>{{item.infoDetails}}</p>
								</figcaption>
							</figure>
						</div>
					</div>
					<!-- Add Pagination -->
					<div class="swiper-pagination"></div>
				</div>
			</el-card>
			<div class="two-right">
				<el-card class="access car_into" :body-style="{ padding: '0px' }">
					<img src="../../../static/img/car_into.png" />
				</el-card>
				<el-card class="access pile_into" :body-style="{ padding: '0px' }" ref="pileInto">
					<img src="../../../static/img/pile_into.png" />
				</el-card>
			</div>
			<div style="clear: both;"></div>
		</div>

		<div class="section-three">
			<div class="flex_div">
				<div class="flex_item">
					<img src="../../../static/img/line_left.png"/>
				</div>
				<div class="flex_item" v-for="(item,index) of indexData.threeItems" :key="index">
					<h4 class="item_name">{{item.itemName}}</h4>
				</div>
				<div class="flex_item">
					<img src="../../../static/img/line_right.png"/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import sectionOne from '../common/section-one.vue';
	import Swiper from 'swiper';
	import 'swiper/dist/css/swiper.min.css';
	export default {
		components: {
			sectionOne
		},
		data() {
			return {
				indexData: []
			}
		},
		created() {
			this.$axios.get('/api/indexData').then(res => { //webName
				this.indexData = res.data.data;
				console.log(this.indexData);
			}).catch(err => {
				console.log(err);
			});
		},
		mounted() {
			setTimeout(function() {
				var swiperObj = new Swiper('#swiper', {
					loop: true,
					pagination: '.swiper-pagination',
					autoplay: 4000,
					paginationClickable: true
				});
			}, 100);
		},
		methods: {

		},
		computed: {

		}
	}
</script>

<style lang="less">
	.section {
		width: 100%;
		.section-one {
			position: relative;
			.secOneData {
				width: 100%;
				position: absolute;
				top: 0;
				.p_title {
					width: 100%;
					text-align: center;
					margin-top: 2.5%;
					font-size: 30px;
					letter-spacing: 6px;
					color: #444;
				}
				.p_sub {
					width: 100%;
					text-align: center;
					margin-top: 1%;
					font-size: 20px;
					color: #777;
				}
				.bottom_data {
					width: 73%;
					margin: 0 auto;
					@media (max-width: 1200px) {
						.el-row {
							margin-top: 3%;
						}
					}
					@media (min-width: 1201px) {
						.el-row {
							margin-top: 5%;
						}
					}
					.grid-content {
						min-height: 36px;
						text-align: center;
					}
					.col_val {
						font-size: 60px;
						color: rgb(255, 122, 21);
					}
					.col_name {
						font-size: 26px;
						color: #444;
					}
				}
			}
		}
		.section-two {
			width: 75%;
			min-width: 1200px;
			margin: 30px auto;
			.el-card {
				float: left;
				width: 72.4%;
				height: auto;
				/*box-shadow: 0 5px 5px 2px rgb(230, 230, 230);*/
				/*border-radius: 3px;*/
				.swiper-pagination-bullet {
					width: 10%;
					height: 10px;
					display: inline-block;
					border-radius: 5px;
					background: #777;
					opacity: 0.5;
				}
				.swiper-pagination-bullet-active {
					opacity: 1;
					background: #007aff;
				}
				figcaption {
					padding: 10px;
					height: 100px;
					h2 {
						font-size: 20px;
						color: rgb(255, 144, 0);
					}
					p {
						font-size: 16px;
						color: #999;
						line-height: 2;
					}
				}
			}
			.two-right {
				float: right;
				width: 24.2%;
				height: auto;
				.access {
					width: 100%;
					height: 230px;
					img {
						width: 100%;
					}
				}
				.pile_into {
					margin-top: 24px;
				}
			}
		}
		.section-three {
			width: 75%;
			min-width: 1200px;
			height: 500px;
			margin: 30px auto;
			.flex_div {
				width: 80%;
				margin: 0 auto;
				display: flex;
				.flex_item {
					flex: 1;
					text-align: center;
					margin: 30px auto;
					font-size: 20px;
				}
			}
		}
	}
</style>


